import { loadStripe } from '@stripe/stripe-js';
import { Elements } from '@stripe/react-stripe-js';

const stripePromise = loadStripe('pk_test_твой_ключ');

const Payment = () => {
  return (
    <Elements stripe={stripePromise}>
      <h2>Payment Page</h2>
      {/* Форма оплаты */}
    </Elements>
  );
};

export default Payment;